.footer {
	@include f(14px, 400, 19px);
	&__inner {
		padding-top: 55px;
		padding-bottom: 35px; }
	&__subtitle {
		display: block;
		color: inherit;
		font-weight: 600; }
	&__item {
		padding: 6px 0 5px; }
	&__link {
		color: inherit;
		opacity: 0.7;
		transition: color 0.3s, opacity 0.3s;
		@include hover {
			color: $blue;
			opacity: 1; } }
	&__phone {
		display: block;
		padding: 6px 0 5px;
		color: inherit;
		@include f(18px, 700, 19px);
		text-transform: uppercase;
		transition: color 0.3s;
		@include hover {
			color: $blue; } }
	&__mail {
		display: block;
		color: inherit;
		transition: color 0.3s;
		@include hover {
			color: $blue; }
		strong {
			font-weight: 600; } }
	&__meta {
		padding: 6px 0 5px;
		opacity: 0.7; }
	&__row {
		&:not(:last-child) {
			margin-bottom: 30px; } }
	&__nav {
		float: left;
		width: 34%; }
	&__col {
		float: left;
		padding-right: 20px;
		width: 50%; }
	&__list {
		padding-top: 9px; }
	&__info {
		float: left;
		padding-right: 20px;
		padding-left: 100px;
		width: 42%; }
	&__media {
		float: right;
		width: 24%;
		max-width: 180px; }
	&__bottom {
		clear: both;
		padding-top: 19px;
		@include clr; }
	&__market {
		width: 100%;
		img {
			display: block;
			max-width: 100%; } }
	&__copy {
		float: left;
		padding-top: 6px;
		padding-right: 20px;
		width: 17%;
		opacity: 0.5; }
	&__rules {
		float: left;
		padding-top: 6px;
		padding-right: 20px;
		width: 25%;
		&-link {
			color: inherit;
			opacity: 0.5;
			transition: color 0.3s, opacity 0.3s;
			@include hover {
				color: $blue;
				opacity: 1; } } }
	&__payments {
		float: left;
		width: 58%;
		font-size: 0; }
	&__payment,
	&__payment-link {
		display: inline-block;
		vertical-align: middle; }
	&__payment {
		padding-right: 28px;
		img {
			display: block; } } }

a.footer__subtitle {
	transition: color 0.3s;
	@include hover {
		color: $blue; } }

.social {
	margin: 10px -14px 23px;
	font-size: 0;
	&__item,
	&__link {
		display: inline-block;
		vertical-align: top; }
	&__link {
		padding: 14px;
		height: 55px;
		color: inherit;
		opacity: 0.5;
		@include vertical;
		transition: color 0.3s, opacity 0.3s;
		@include hover {
			color: $blue;
			opacity: 1; } }
	&__icon {
		display: inline-block;
		vertical-align: middle;
		fill: currentColor; }
	&__instagram {
		font-size: 27px; }
	&__vk {
		font-size: 16px; }
	&__facebook {
		font-size: 26px; } }

.go-top {
	position: absolute;
	top: 0;
	left: 100%;
	border-radius: 50%;
	width: 67px;
	height: 67px;
	color: $white;
	background: $blue;
	@include vertical;
	transform: translate3d(63px, -50%, 0);
	transition: color 0.3s, background 0.3s;
	@include hover {
		background: $lightBlue; }
	&__icon {
		display: inline-block;
		vertical-align: middle;
		fill: currentColor;
		font-size: 9px;
		transform: translateY(-2px); } }
