/* This is the core CSS of Tooltipster */
/* GENERAL STRUCTURE RULES (do not edit this section) */
.tooltipster-base {
  /* this ensures that a constrained height set by functionPosition,
	if greater that the natural height of the tooltip, will be enforced
	in browsers that support display:flex */
  display: flex;
  pointer-events: none;
  /* this may be overriden in JS for fixed position origins */
  position: absolute;
}

.tooltipster-box {
  /* see .tooltipster-base. flex-shrink 1 is only necessary for IE10-
	and flex-basis auto for IE11- (at least) */
  flex: 1 1 auto;
}

.tooltipster-content {
  /* prevents an overflow if the user adds padding to the div */
  box-sizing: border-box;
  /* these make sure we'll be able to detect any overflow */
  max-height: 100%;
  max-width: 100%;
  overflow: auto;
}

.tooltipster-ruler {
  /* these let us test the size of the tooltip without overflowing the window */
  bottom: 0;
  left: 0;
  overflow: hidden;
  position: fixed;
  right: 0;
  top: 0;
  visibility: hidden;
}

/* ANIMATIONS */
/* Open/close animations */
/* fade */
.tooltipster-fade {
  opacity: 0;
  -webkit-transition-property: opacity;
  -moz-transition-property: opacity;
  -o-transition-property: opacity;
  -ms-transition-property: opacity;
  transition-property: opacity;
}

.tooltipster-fade.tooltipster-show {
  opacity: 1;
}

/* grow */
.tooltipster-grow {
  -webkit-transform: scale(0, 0);
  -moz-transform: scale(0, 0);
  -o-transform: scale(0, 0);
  -ms-transform: scale(0, 0);
  transform: scale(0, 0);
  -webkit-transition-property: -webkit-transform;
  -moz-transition-property: -moz-transform;
  -o-transition-property: -o-transform;
  -ms-transition-property: -ms-transform;
  transition-property: transform;
  -webkit-backface-visibility: hidden;
}

.tooltipster-grow.tooltipster-show {
  -webkit-transform: scale(1, 1);
  -moz-transform: scale(1, 1);
  -o-transform: scale(1, 1);
  -ms-transform: scale(1, 1);
  transform: scale(1, 1);
  -webkit-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1);
  -webkit-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.15);
  -moz-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.15);
  -ms-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.15);
  -o-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.15);
  transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.15);
}

/* swing */
.tooltipster-swing {
  opacity: 0;
  -webkit-transform: rotateZ(4deg);
  -moz-transform: rotateZ(4deg);
  -o-transform: rotateZ(4deg);
  -ms-transform: rotateZ(4deg);
  transform: rotateZ(4deg);
  -webkit-transition-property: -webkit-transform, opacity;
  -moz-transition-property: -moz-transform;
  -o-transition-property: -o-transform;
  -ms-transition-property: -ms-transform;
  transition-property: transform;
}

.tooltipster-swing.tooltipster-show {
  opacity: 1;
  -webkit-transform: rotateZ(0deg);
  -moz-transform: rotateZ(0deg);
  -o-transform: rotateZ(0deg);
  -ms-transform: rotateZ(0deg);
  transform: rotateZ(0deg);
  -webkit-transition-timing-function: cubic-bezier(0.23, 0.635, 0.495, 1);
  -webkit-transition-timing-function: cubic-bezier(0.23, 0.635, 0.495, 2.4);
  -moz-transition-timing-function: cubic-bezier(0.23, 0.635, 0.495, 2.4);
  -ms-transition-timing-function: cubic-bezier(0.23, 0.635, 0.495, 2.4);
  -o-transition-timing-function: cubic-bezier(0.23, 0.635, 0.495, 2.4);
  transition-timing-function: cubic-bezier(0.23, 0.635, 0.495, 2.4);
}

/* fall */
.tooltipster-fall {
  -webkit-transition-property: top;
  -moz-transition-property: top;
  -o-transition-property: top;
  -ms-transition-property: top;
  transition-property: top;
  -webkit-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1);
  -webkit-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.15);
  -moz-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.15);
  -ms-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.15);
  -o-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.15);
  transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.15);
}

.tooltipster-fall.tooltipster-initial {
  top: 0 !important;
}

.tooltipster-fall.tooltipster-dying {
  -webkit-transition-property: all;
  -moz-transition-property: all;
  -o-transition-property: all;
  -ms-transition-property: all;
  transition-property: all;
  top: 0 !important;
  opacity: 0;
}

/* slide */
.tooltipster-slide {
  -webkit-transition-property: left;
  -moz-transition-property: left;
  -o-transition-property: left;
  -ms-transition-property: left;
  transition-property: left;
  -webkit-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1);
  -webkit-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.15);
  -moz-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.15);
  -ms-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.15);
  -o-transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.15);
  transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.15);
}

.tooltipster-slide.tooltipster-initial {
  left: -40px !important;
}

.tooltipster-slide.tooltipster-dying {
  -webkit-transition-property: all;
  -moz-transition-property: all;
  -o-transition-property: all;
  -ms-transition-property: all;
  transition-property: all;
  left: 0 !important;
  opacity: 0;
}

/* Update animations */
/* We use animations rather than transitions here because
 transition durations may be specified in the style tag due to
 animationDuration, and we try to avoid collisions and the use
 of !important */
/* fade */
@keyframes tooltipster-fading {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.tooltipster-update-fade {
  animation: tooltipster-fading 400ms;
}

/* rotate */
@keyframes tooltipster-rotating {
  25% {
    transform: rotate(-2deg);
  }
  75% {
    transform: rotate(2deg);
  }
  100% {
    transform: rotate(0);
  }
}

.tooltipster-update-rotate {
  animation: tooltipster-rotating 600ms;
}

/* scale */
@keyframes tooltipster-scaling {
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

.tooltipster-update-scale {
  animation: tooltipster-scaling 600ms;
}

/**
 * DEFAULT STYLE OF THE SIDETIP PLUGIN
 * 
 * All styles are "namespaced" with .tooltipster-sidetip to prevent
 * conflicts between plugins.
 */
/* .tooltipster-box */
.tooltipster-sidetip .tooltipster-box {
  background: #565656;
  border: 2px solid black;
  border-radius: 4px;
}

.tooltipster-sidetip.tooltipster-bottom .tooltipster-box {
  margin-top: 8px;
}

.tooltipster-sidetip.tooltipster-left .tooltipster-box {
  margin-right: 8px;
}

.tooltipster-sidetip.tooltipster-right .tooltipster-box {
  margin-left: 8px;
}

.tooltipster-sidetip.tooltipster-top .tooltipster-box {
  margin-bottom: 8px;
}

/* .tooltipster-content */
.tooltipster-sidetip .tooltipster-content {
  color: white;
  line-height: 18px;
  padding: 6px 14px;
}

/* .tooltipster-arrow : will keep only the zone of .tooltipster-arrow-uncropped that
corresponds to the arrow we want to display */
.tooltipster-sidetip .tooltipster-arrow {
  overflow: hidden;
  position: absolute;
}

.tooltipster-sidetip.tooltipster-bottom .tooltipster-arrow {
  height: 10px;
  /* half the width, for centering */
  margin-left: -10px;
  top: 0;
  width: 20px;
}

.tooltipster-sidetip.tooltipster-left .tooltipster-arrow {
  height: 20px;
  margin-top: -10px;
  right: 0;
  /* top 0 to keep the arrow from overflowing .tooltipster-base when it has not
	been positioned yet */
  top: 0;
  width: 10px;
}

.tooltipster-sidetip.tooltipster-right .tooltipster-arrow {
  height: 20px;
  margin-top: -10px;
  left: 0;
  /* same as .tooltipster-left .tooltipster-arrow */
  top: 0;
  width: 10px;
}

.tooltipster-sidetip.tooltipster-top .tooltipster-arrow {
  bottom: 0;
  height: 10px;
  margin-left: -10px;
  width: 20px;
}

/* common rules between .tooltipster-arrow-background and .tooltipster-arrow-border */
.tooltipster-sidetip .tooltipster-arrow-background, .tooltipster-sidetip .tooltipster-arrow-border {
  height: 0;
  position: absolute;
  width: 0;
}

/* .tooltipster-arrow-background */
.tooltipster-sidetip .tooltipster-arrow-background {
  border: 10px solid transparent;
}

.tooltipster-sidetip.tooltipster-bottom .tooltipster-arrow-background {
  border-bottom-color: #565656;
  left: 0;
  top: 3px;
}

.tooltipster-sidetip.tooltipster-left .tooltipster-arrow-background {
  border-left-color: #565656;
  left: -3px;
  top: 0;
}

.tooltipster-sidetip.tooltipster-right .tooltipster-arrow-background {
  border-right-color: #565656;
  left: 3px;
  top: 0;
}

.tooltipster-sidetip.tooltipster-top .tooltipster-arrow-background {
  border-top-color: #565656;
  left: 0;
  top: -3px;
}

/* .tooltipster-arrow-border */
.tooltipster-sidetip .tooltipster-arrow-border {
  border: 10px solid transparent;
  left: 0;
  top: 0;
}

.tooltipster-sidetip.tooltipster-bottom .tooltipster-arrow-border {
  border-bottom-color: black;
}

.tooltipster-sidetip.tooltipster-left .tooltipster-arrow-border {
  border-left-color: black;
}

.tooltipster-sidetip.tooltipster-right .tooltipster-arrow-border {
  border-right-color: black;
}

.tooltipster-sidetip.tooltipster-top .tooltipster-arrow-border {
  border-top-color: black;
}

/* tooltipster-arrow-uncropped */
.tooltipster-sidetip .tooltipster-arrow-uncropped {
  position: relative;
}

.tooltipster-sidetip.tooltipster-bottom .tooltipster-arrow-uncropped {
  top: -10px;
}

.tooltipster-sidetip.tooltipster-right .tooltipster-arrow-uncropped {
  left: -10px;
}

body {
  min-width: 1600px;
  color: #0f2841;
  background: #fff;
  font-family: "Open Sans", Helvetica, sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 1.734;
}

.main {
  padding-top: 17px;
}

.inner {
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1210px;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.section:not(:last-child) {
  margin-bottom: 20px;
}

.title-lg {
  font-family: "Montserrat", Helvetica, sans-serif;
  font-size: 21px;
  font-weight: 500;
  line-height: 1.715;
  letter-spacing: 0.025em;
}

.text-uppercase {
  text-transform: uppercase;
}

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 10px 21px 11px;
  border-radius: 5px;
  font-family: "Open Sans", Helvetica, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.01em;
  transition: background 0.3s;
}

.btn_blue {
  color: #fff;
  background: #0caafe;
}

.no-touch .btn_blue:hover {
  background: #37baff;
}

.btn_orange {
  color: #fff;
  background: #ffae1f;
}

.no-touch .btn_orange:hover {
  background: #ffc051;
}

.header {
  padding: 18px 30px 27px;
  border-bottom: 1px solid #dcdcdc;
  font-size: 0;
}

.header__inner {
  margin: 0 auto;
  max-width: 1540px;
}

.header__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.header__btns {
  float: left;
  padding: 8px 20px 0 78px;
}

.header__btn {
  margin: 5px;
}

.header__actions {
  float: right;
  padding: 10px 0 1px;
  margin: 0 -16px 0 29px;
}

.header__action {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 10px 16px;
  height: 40px;
  color: inherit;
  text-align: center;
  font-size: 0;
  transition: color 0.3s;
}

.header__action:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.no-touch .header__action:hover {
  color: #ffae1f;
}

.header__icon {
  display: inline-block;
  vertical-align: middle;
  font-size: 20px;
  fill: currentColor;
}

.header__sticker {
  position: absolute;
  top: -3px;
  right: 0;
  border-radius: 13px;
  min-width: 25px;
  color: #fff;
  background: #ffae1f;
  font-size: 12px;
  font-weight: 400;
  line-height: 25px;
}

.header__info {
  float: right;
  padding: 13px 0 0;
}

.header__phone {
  display: block;
  color: inherit;
  font-size: 22px;
  font-weight: 700;
  line-height: 19px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
  transition: color 0.3s;
}

.no-touch .header__phone:hover {
  color: #0caafe;
}

.header__schedule {
  margin-top: 9px;
  opacity: 0.5;
  font-size: 12px;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.01em;
}

.logo {
  float: left;
  font-size: 0;
}

.nav {
  overflow: visible;
  text-align: center;
  padding: 13px 20px 5px 0;
}

.nav__item {
  display: inline-block;
  vertical-align: top;
}

.nav__link {
  display: block;
  padding: 10px 15px 11px;
  color: currentColor;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.01em;
  transition: color 0.3s;
}

.no-touch .nav__link:hover {
  color: #0caafe;
}

.footer {
  font-size: 14px;
  font-weight: 400;
  line-height: 19px;
}

.footer__inner {
  padding-top: 55px;
  padding-bottom: 35px;
}

.footer__subtitle {
  display: block;
  color: inherit;
  font-weight: 600;
}

.footer__item {
  padding: 6px 0 5px;
}

.footer__link {
  color: inherit;
  opacity: 0.7;
  transition: color 0.3s, opacity 0.3s;
}

.no-touch .footer__link:hover {
  color: #0caafe;
  opacity: 1;
}

.footer__phone {
  display: block;
  padding: 6px 0 5px;
  color: inherit;
  font-size: 18px;
  font-weight: 700;
  line-height: 19px;
  text-transform: uppercase;
  transition: color 0.3s;
}

.no-touch .footer__phone:hover {
  color: #0caafe;
}

.footer__mail {
  display: block;
  color: inherit;
  transition: color 0.3s;
}

.no-touch .footer__mail:hover {
  color: #0caafe;
}

.footer__mail strong {
  font-weight: 600;
}

.footer__meta {
  padding: 6px 0 5px;
  opacity: 0.7;
}

.footer__row:not(:last-child) {
  margin-bottom: 30px;
}

.footer__nav {
  float: left;
  width: 34%;
}

.footer__col {
  float: left;
  padding-right: 20px;
  width: 50%;
}

.footer__list {
  padding-top: 9px;
}

.footer__info {
  float: left;
  padding-right: 20px;
  padding-left: 100px;
  width: 42%;
}

.footer__media {
  float: right;
  width: 24%;
  max-width: 180px;
}

.footer__bottom {
  clear: both;
  padding-top: 19px;
}

.footer__bottom:after {
  content: " ";
  display: table;
  clear: both;
}

.footer__market {
  width: 100%;
}

.footer__market img {
  display: block;
  max-width: 100%;
}

.footer__copy {
  float: left;
  padding-top: 6px;
  padding-right: 20px;
  width: 17%;
  opacity: 0.5;
}

.footer__rules {
  float: left;
  padding-top: 6px;
  padding-right: 20px;
  width: 25%;
}

.footer__rules-link {
  color: inherit;
  opacity: 0.5;
  transition: color 0.3s, opacity 0.3s;
}

.no-touch .footer__rules-link:hover {
  color: #0caafe;
  opacity: 1;
}

.footer__payments {
  float: left;
  width: 58%;
  font-size: 0;
}

.footer__payment, .footer__payment-link {
  display: inline-block;
  vertical-align: middle;
}

.footer__payment {
  padding-right: 28px;
}

.footer__payment img {
  display: block;
}

a.footer__subtitle {
  transition: color 0.3s;
}

.no-touch a.footer__subtitle:hover {
  color: #0caafe;
}

.social {
  margin: 10px -14px 23px;
  font-size: 0;
}

.social__item, .social__link {
  display: inline-block;
  vertical-align: top;
}

.social__link {
  padding: 14px;
  height: 55px;
  color: inherit;
  opacity: 0.5;
  text-align: center;
  font-size: 0;
  transition: color 0.3s, opacity 0.3s;
}

.social__link:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.no-touch .social__link:hover {
  color: #0caafe;
  opacity: 1;
}

.social__icon {
  display: inline-block;
  vertical-align: middle;
  fill: currentColor;
}

.social__instagram {
  font-size: 27px;
}

.social__vk {
  font-size: 16px;
}

.social__facebook {
  font-size: 26px;
}

.go-top {
  position: absolute;
  top: 0;
  left: 100%;
  border-radius: 50%;
  width: 67px;
  height: 67px;
  color: #fff;
  background: #0caafe;
  text-align: center;
  font-size: 0;
  transform: translate3d(63px, -50%, 0);
  transition: color 0.3s, background 0.3s;
}

.go-top:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.no-touch .go-top:hover {
  background: #37baff;
}

.go-top__icon {
  display: inline-block;
  vertical-align: middle;
  fill: currentColor;
  font-size: 9px;
  transform: translateY(-2px);
}

.search__form {
  position: relative;
}

.search__input {
  display: block;
  padding-left: 20px;
  padding-right: 55px;
  border: 2px solid #0caafe;
  border-radius: 5px;
  width: 100%;
  height: 54px;
  color: #0f2841;
  background: #fff;
  font-family: "Open Sans", Helvetica, sans-serif;
  font-size: 17px;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 0.01em;
}

.search__input::placeholder {
  color: #0f2841;
  opacity: 0.5;
}

.search__btn {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  padding-bottom: 2px;
  width: 55px;
  background: none;
  text-align: center;
  font-size: 0;
}

.search__btn:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.search__icon {
  display: inline-block;
  vertical-align: middle;
  fill: #3aa0d1;
  font-size: 21px;
}

.banner {
  display: block;
  color: #fff;
}

.no-touch .banner:hover .banner__link:before {
  opacity: 0;
}

.banner_blue {
  background-image: linear-gradient(29deg, #0caafe 0%, #2df0f2 100%);
}

.banner_green {
  background-image: linear-gradient(29deg, #00ab89 0%, #3affd3 100%);
}

.banner_orange {
  background-image: linear-gradient(29deg, #ff881f 0%, #ffcf3c 100%);
}

.banner__inner {
  position: relative;
  height: 217px;
}

.banner__content {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 21px 30px;
}

.banner__title {
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: -0.025em;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.banner__link {
  position: relative;
  display: inline-block;
  vertical-align: top;
  font-size: 14px;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0.01em;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.banner__link:before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 5px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  transition: opacity 0.3s;
}

.banner__baxi {
  position: absolute;
  top: 45px;
  left: 30px;
}

.banner__item {
  position: absolute;
  top: 45px;
  right: 44px;
}

.banner__item ~ .banner__content {
  padding-right: 130px;
}

.banner__rehau {
  position: absolute;
  top: 29px;
  left: 30px;
}

.banner__card {
  position: absolute;
  top: 14px;
  left: 30px;
}

.banners {
  margin: -1px;
  display: flex;
}

.banners__col {
  padding: 1px;
  width: 33.33%;
}

.info__header {
  padding-top: 30px;
  padding-bottom: 30px;
  text-align: center;
}

.info__title {
  margin-bottom: 7px;
}

.info__description {
  letter-spacing: 0.01em;
}

.info__body {
  padding-top: 37px;
  padding-bottom: 30px;
  background: #efefef;
}

.info__items {
  font-size: 0;
  text-align: center;
}

.info__item {
  display: inline-block;
  vertical-align: top;
  padding: 35px 20px;
  width: 25%;
}

.info__figure {
  position: relative;
  margin: 0 auto 29px;
  border-radius: 50%;
  width: 188px;
  height: 188px;
  background: #fff;
  text-align: center;
  font-size: 0;
}

.info__figure:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.info__figure:after {
  content: '';
  position: absolute;
  top: 7px;
  left: 7px;
  right: 7px;
  bottom: 7px;
  border: 1px solid gainsboro;
  border-radius: 50%;
}

.info__icon {
  display: inline-block;
  vertical-align: middle;
  fill: #0caafe;
  font-size: 60px;
}

.info__subtitle {
  margin: 0 auto;
  max-width: 210px;
  font-size: 12px;
  line-height: 19px;
  letter-spacing: 0.15em;
  text-transform: uppercase;
}

.main-tabs {
  padding: 20px 0;
  border-bottom: 1px solid #dcdcdc;
}

.main-tabs__controls {
  margin: -10px;
}

.main-tabs__controls:after {
  content: " ";
  display: table;
  clear: both;
}

.main-tabs__group {
  position: relative;
  float: left;
  padding: 10px;
  width: 50%;
}

.main-tabs__group:before {
  content: '';
  position: absolute;
  z-index: 1;
  top: 10px;
  bottom: 10px;
  left: 50%;
  border-left: 1px solid gainsboro;
}

.main-tabs__button {
  position: relative;
  float: left;
  border: 1px solid gainsboro;
  width: 50%;
  height: 54px;
  color: rgba(15, 40, 65, 0.7);
  background: none;
  letter-spacing: 0.01em;
  text-align: center;
  font-size: 0;
  transition: border-color 0.3s, color 0.3s, background 0.3s;
}

.main-tabs__button:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.main-tabs__button:first-child {
  border-right: 0;
  border-radius: 5px 0 0 5px;
}

.main-tabs__button:last-child {
  border-left: 0;
  border-radius: 0 5px 5px 0;
}

.no-touch .main-tabs__button:hover {
  color: #0caafe;
  background-color: #f1f1f1;
}

.main-tabs__button.is-active {
  border-color: #0caafe;
  color: #fff;
  background: #0caafe;
}

.no-touch .main-tabs__button.is-active:hover {
  color: #fff;
  background: #37baff;
}

.main-tabs__button_strong {
  font-weight: 700;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}

.main-tabs__button span {
  font-size: 15px;
  line-height: normal;
}

.main-tabs__button-in, .main-tabs__menu, .main-tabs__icon {
  display: inline-block;
  vertical-align: middle;
}

.main-tabs__icon {
  margin-right: 10px;
  fill: currentColor;
  font-size: 27px;
}

.main-tabs__menu {
  margin-right: 15px;
  border-top: 1px solid currentColor;
  width: 23px;
}

.main-tabs__menu:before, .main-tabs__menu:after {
  content: '';
  display: block;
  margin-top: 4px;
  border-top: 1px solid currentColor;
}

.main-catalog {
  padding-top: 3px;
}

.main-catalog__categories {
  clear: both;
  margin: 0 -10px;
  font-size: 0;
}

@supports (column-count: 4) {
  .main-catalog__categories {
    column-count: 4;
  }
}

.main-catalog__category {
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding: 27px 10px;
  width: 25%;
  font-size: 16px;
  font-weight: 400;
  line-height: 22px;
}

@supports (column-count: 4) {
  .main-catalog__category {
    display: list-item;
    width: auto;
    break-inside: avoid-column;
  }
}

.main-catalog__category-in {
  position: relative;
  padding-left: 70px;
}

.main-catalog__figure {
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  font-size: 0;
  text-align: center;
}

.main-catalog__title {
  display: block;
  margin-bottom: 12px;
  color: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3125;
  transition: color 0.3s;
}

.no-touch .main-catalog__title:hover {
  color: #0caafe;
}

.main-catalog__item {
  padding: 5px 0;
}

.main-catalog__link {
  display: block;
  color: inherit;
  font-size: 13px;
  line-height: 1.6;
  letter-spacing: 0.01em;
  transition: color 0.3s;
}

.no-touch .main-catalog__link:hover {
  color: #0caafe;
}
