body {
	min-width: 1600px;
	color: $dark;
	background: $white;
	font-family: $opensans;
	@include f(15px, 400, 1.734); }

.main {
	padding-top: 17px; }

.inner {
	position: relative;
	padding-left: 15px;
	padding-right: 15px;
	margin-left: auto;
	margin-right: auto;
	max-width: 1210px;
	@include clr; }

.section {
	&:not(:last-child) {
		margin-bottom: 20px; } }
